$type-base-size: 16px;
$type-baseline: 22px;
$type-scale: 1.414;
$font-font-primary: Georgia, Times New Roman, serif;
$font-font-secondary: Helvetica, Arial, sans-serif;
$whitespace-margin: 1;
$whitespace-padding: 1;
$color-primary: #f26322;
$color-secondary: #310031;
$color-base: #000000;
// Example Sass file
body {
	font-size: $type-base-size;
	font-family: Garamond;
}